import * as React from "react"
// import useMouse from "@react-hook/mouse-position"
import { useState, useEffect } from "react"
import { motion } from "framer-motion"
import { useMousePosition } from "./useMousePosition";

function Cursor(props) {
  const position = useMousePosition();

  if (typeof window === "undefined") return null;

  let mouseXPosition = position.x;
  let mouseYPosition = position.y;
  
  const variants = {
    default: {
      opacity: 1,
      height: 10,
      width: 10,
      fontSize: "16px",
      backgroundColor: "rgba(30, 145, 214, 1)",
      x: mouseXPosition - 6,
      y: mouseYPosition - 6,
      transition: {
        type: "spring",
        mass: 0.4,
        stiffness: 130,
      }
    },
    project: {
      opacity: 1,
      backgroundColor: "rgba(255, 255, 255, 0.9)",
      color: "#000",
      height: 80,
      width: 80,
      fontSize: "18px",
      x: mouseXPosition - 60,
      y: mouseYPosition - 60,
      transition: {
        type: "spring",
        mass: 0.4,
        stiffness: 130,
        duration: 0.3,
      }
    },
    contact: {
      opacity: 1,
      backgroundColor: "rgba(255, 189, 189, 0.85)",
      color: "#000",
      height: 64,
      width: 64,
      fontSize: "32px",
      x: mouseXPosition - 48,
      y: mouseYPosition - 48,
      transition: {
        type: "spring",
        mass: 0.4,
        stiffness: 130,
      }
    },
    link: {
      opacity: 1,
      backgroundColor: "rgba(30, 145, 214, 0.1)",
      color: "#000",
      height: 80,
      width: 80,
      zIndex: 1,
      fontSize: "32px",
      x: mouseXPosition - 40,
      y: mouseYPosition - 40,
      transition: {
        type: "spring",
        mass: 0.4,
        stiffness: 130,
      }
    },
    initial: {
      opacity: 0,
      height: 10,
      width: 10,
      fontSize: "16px",
      backgroundColor: "#1e91d6",
      x: "50%",
      y: "50%",
      transition: {
        type: "spring",
        mass: 0,
        stiffness: 0,
        duration: 0.1,
      }
    },
  };
  
  const spring = {
    type: "spring",
    stiffness: 500,
    damping: 28,
    mass: 0.5,
  };

  return (
    <motion.div
          variants={variants}
          className="circle"
          animate={props.cursorVariant}
          transition={spring}
        >
          <span className="cursor-text">{props.cursorText}</span>
        </motion.div>
  )
}

export default Cursor